@import '_base.css';

/* ---------------------------------------- */

/* HEADER */

/* ---------------------------------------- */

header {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(49, 21, 46, 0.55)), to(rgba(49, 21, 46, 0.55))), url(../assets/img/background-min.jpeg);
    background-image: linear-gradient(rgba(49, 21, 46, 0.55), rgba(49, 21, 46, 0.55)), url(../assets/img/background-min.jpeg);
    background-size: cover;
    background-position: center;
    height: 100vh;
    opacity: 0.8;
    filter: alpha(opacity=80);
    /* For IE8 and earlier */
    position: relative;
    padding-top: 10px;
}

.header-text--box {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.typewrite {
    color: #fff;
    -webkit-box-shadow: .5px .5px fff;
    box-shadow: .5px .5px fff;
}

/* --- Mobile Nav --- */

.nav-link--box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding-top: 10px;
}

.mobile-nav-icon {
    display: inline-block;
    padding: 22px 0 0 20px;
}

/* --- Buttons --- */

.download-link-mobile {
    display: inline-block;
    text-decoration: none;
    -webkit-box-shadow: .5px .5px fff;
    box-shadow: .5px .5px fff;
    color: #fff;
}

.download-link {
    display: none;
}

/* --- Icons --- */

.triangle--header {
    position: absolute;
    top: 95%;
    left: 50%;
    -webkit-transform: translate(-95%, -45%);
    transform: translate(-95%, -45%);
    height: 25px;
    width: 25px;
    fill: var(--main-blue);
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}

.triangle--header:hover {
    fill: rgb(1, 102, 197);
    -webkit-transition: fill .3s;
    transition: fill .3s;
}

/* --- Main Nav --- */

.main-nav--box--triangle {
    margin-bottom: -9px;
}

.main-nav--box--triangle-shape {
    height: 20px;
    width: 20px;
    background: #fefdff;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    margin-left: 31px;
    display: none;
    cursor: pointer;
}

.open {
    display: block;
}

.main-nav--box {
    background-color: #fefdff;
}

.main-nav {
    display: none;
    -webkit-transition: all .5s cubic-bezier(0, 1, 0.5, 1);
    transition: all .5s cubic-bezier(0, 1, 0.5, 1);
    list-style: none;
}

.main-nav.open {
    display: block;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    list-style: none;
    padding: 13px 1em;
    text-transform: uppercase;
    font-size: 1.1em;
}

.main-nav li a {
    color: #333;
}

/* --- Sticky Nav --- */

.sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.98);
    -webkit-box-shadow: 0 2px 2px #efefef;
    box-shadow: 0 2px 2px #efefef;
    z-index: 9999;
    margin-top: 18px;
    overflow: hidden;
}

@media screen and (min-width: 500px) {
    /* --- Heading --- */
    h1 {
        font-size: 5.8em;
    }
    .h2 {
        font-size: 2em;
    }
    h3 {
        font-size: 3.4em;
    }
    .section-offer h3,
    .section-portfolio h3 {
        font-size: 3.7em;
    }
    h4,
    h5 {
        font-size: 2em;
    }
    /* --- buttons --- */
    .btn {
        padding: 6px;
    }
    /* --- Nav --- */
    .mobile-nav-icon img {
        height: 20px;
    }
}

@media screen and (min-width: 640px) {
    /* --- Heading --- */
    h1 {
        font-size: 7em;
    }
}

@media screen and (min-width: 900px) {
    /* --- Nav --- */
    .nav-container {
        padding: 1.5em;
    }
    .mobile-nav-icon {
        display: none;
    }
    .nav-link--box {
        display: none;
    }
    .main-nav li a {
        color: #fff;
    }
    .main-nav--box--triangle {
        display: none;
    }
    .main-nav--box {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        background-color: transparent;
    }
    .main-nav {
        display: block;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
        justify-content: space-around;
        list-style: none;
        padding: 13px 1em;
        text-transform: uppercase;
        font-size: 1.1em;
        text-shadow: .2px .2px #333;
    }
    .main-nav li {
        margin-right: 45px;
    }
    .main-nav li:first-child,
    .main-nav li:nth-child(2) {
        border: 2px solid transparent;
        padding: 12px 5px;
    }
    .main-nav li:last-child {
        border: 3px solid var(--main-blue);
        padding: 12px 10px 8px 10px;
        margin-right: 0;
    }
    .main-nav li:last-child:hover {
        border-color: rgb(1, 102, 197);
        -webkit-transition: fill .3s;
        transition: fill .3s;
    }
    .main-nav li:first-child a:hover,
    .main-nav li:nth-child(2) a:hover {
        border-bottom: 2px solid var(--main-blue);
        padding-bottom: 10px;
    }
    .download-link-mobile {
        display: none;
    }
    .download-link {
        display: inline-block;
        text-decoration: none;
        margin: 1.4em 1em 1.4em 1em;
        padding: 6px;
        -webkit-box-shadow: .5px .5px rgb(34, 69, 109);
        box-shadow: .5px .5px rgb(34, 69, 109);
        color: #000;
    }
}

@media screen and (min-width: 1700px) {
    .h2 {
        font-size: 2.4em;
    }
    /* --- Nav --- */
    .main-nav li a {
        font-size: 1.2em;
    }
    .download-link {
        font-size: 1.2em;
        margin-top: 1.2em;
        margin-bottom: 1.2em;
    }
}